.expense-date{
    display: flex;
    flex-direction: column;
    width: 5.5rem;
    height: 5.5rem;
    border: 1px solid #ececec;
    background-color: #2C3A47;
    box-shadow: 0 5px 8px rgba(0, 0, 0, 0.30);
    color: white;
    border-radius: 12px;
    align-items: center;
    justify-content: center;
}

.expense-date-month{
    font-size: 1.2rem;
    font-weight: bold;
}


.expense-date-year{
    font-size: 1.1rem;
}
.expense-date-day{
    font-size: 1rem;
    font-weight: bold;
}
